import * as React from 'react';
import { Flex, Heading, Box, Text } from '@chakra-ui/core';
import { formatDate, nl2br } from '~/utils';
import SectionTitle from '../SectionTitle';
import Section from '../Section';

export default function News({ data, ...props }) {
  return (
    <Section backgroundColor="blue.50" {...props}>
      <Box mx={[2, 8, 16]}>
        <SectionTitle>News</SectionTitle>
        {data.map((item) => {
          const description = nl2br(item.descrizione);
          const date = formatDate(item.date);

          return (
            <Box p={4} key={item.date} as="article" minHeight="12em">
              <Text fontSize="sm" color="gray.500" mb={2}>
                {date}
              </Text>
              <Flex justifyContent="space-between">
                <Heading size="xs" mb={1}>
                  {item.titolo}
                </Heading>
              </Flex>
              <Text fontSize="sm" dangerouslySetInnerHTML={{ __html: description }} />
            </Box>
          );
        })}
      </Box>
    </Section>
  );
}
